.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  text-transform: uppercase;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list :nth-child(n+2) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  padding-top: 23px;
  padding-bottom: 23px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.logo {
  margin-top: 2px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-bottom: 4px solid;
  border-radius: 2px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

* {
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.card {
  border-radius: 5px;
  width: 200px;
}

.card__container {
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;
}

.card__container:hover {
  color: #34568b;
  transition-duration: .3s;
  transform: scale(1.2);
}

.card__mac-icon {
  max-width: 160px;
  min-height: 134px;
  margin: 0 auto;
  display: block;
}

.card__item {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__item:hover {
  color: #34568b;
  transition-duration: .3s;
}

.card__product {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price--1 {
  color: #616070;
  margin: 0;
}

.card__price--2 {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  font-size: 10px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.btn {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
